section {
   @extend %flex;
   flex: 1 1 auto;

   background: $colorBrandLight;
   &:nth-of-type(even) {
      background: darken($colorBrandLight, 5%);
   }
   &.news .row {
      padding-bottom: 0;
   }
   h2 {
      position: relative;

      min-width: 100%;
      margin-right: 0;
   }
}
